import { Component, OnInit, ViewEncapsulation, OnChanges, Input, TemplateRef, Output, 
  EventEmitter, ViewChild, ViewChildren, QueryList, SimpleChanges } from '@angular/core';
import { DatePipe } from '@angular/common';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { SelectionModel } from '@angular/cdk/collections';
import { BehaviorSubject } from 'rxjs';
import { MatSort, MatPaginator, MatTableDataSource } from '@angular/material';
import { MultiSelectSearchComponent } from './multi-select-search';

export interface AwardDataColumn {
  text: string;
  bindingKey?: string;
  type: ColumnType;
  iconTooltipBindingKey?: string;
  intialSort?: boolean;
  searchVisible?: boolean;
  columnTemplateRef?: TemplateRef<any>;
}

export type SelectMode = 'None' | 'SingleSelect' | 'MultiSelect';
export type ColumnType = 'string' | 'date' | 'template';

interface TableFilter {
  [column: string]: string[];
}

@Component({
  selector: 'rich-data-table',
  templateUrl: './rich-data-table-angular.component.html',
  styleUrls: ['./rich-data-table-angular.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [DatePipe],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0', visibility: 'hidden'})),
      state('expanded', style({height: '*', visibility: 'visible'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})
export class RichDataTableAngularComponent implements OnInit, OnChanges {

  @Input()
  dataSource;

  @Input()
  displayedColumns: Array<AwardDataColumn>;

  @Input()
  mode?: SelectMode;

  @Input()
  selectAll?: boolean;

  @Input()
  expandedRowTemplateRef?: TemplateRef<any>;

  @Input()
  isExpandable?: boolean;

  @Output()
  selectionChange: EventEmitter<object> = new EventEmitter<object>();

  allColumns: string[] = [];
  private filters: TableFilter = {};
  selection: SelectionModel<object>;
  loading$ = new BehaviorSubject<boolean>(true);
  tableData;
  intialSort: string;
  expandedElement: object;

  @ViewChild(MatSort, null) sort: MatSort;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChildren(MultiSelectSearchComponent, null) searchComponents: QueryList<MultiSelectSearchComponent>;


  constructor(public datepipe: DatePipe) {
    this.mode = 'None';
    this.selectAll = false;
    this.loading$.next(true);
    this.isExpandable = false;
    this.tableData = new MatTableDataSource([]);
  }

  ngOnInit() {
    this.selection = new SelectionModel<object>(this.mode === 'MultiSelect', [], true);
    if (this.displayedColumns) {
      this.displayedColumns.map(column => {
        if (!column.bindingKey) {
          column.bindingKey = column.text;
        }
        if (column.searchVisible !== false) {
          column.searchVisible = true;
        }
      });
      this.allColumns = this.allColumns.concat(this.displayedColumns.map(column => column.bindingKey));
      this._handleMode();
      const intialSort = this.displayedColumns.filter(d => d.intialSort === true);
      this.intialSort = intialSort.length ? intialSort[0].bindingKey : '';
    }
  }
  ngOnChanges(changes: SimpleChanges) {
    if (changes.dataSource && changes.dataSource.currentValue) {
      if (changes.dataSource.currentValue !== null) {
        this.loading$.next(false);
      }
      this.selection = new SelectionModel<object>(this.mode === 'MultiSelect', [], true);
      this.tableData = new MatTableDataSource(changes.dataSource.currentValue);
      this.tableData.filterPredicate =
        (data: object, filters: TableFilter) => {
          return this.getTableFilterPredicate(data, filters);
        };
      this.tableData.paginator = this.paginator;
      this.tableData.sort = this.sort;
      this.clearFilter();
      if (this.selectAll && this.mode === 'MultiSelect') {
        this.tableData.data.forEach(row => this.selection.select(row));
      }
    }
  }
  /**
   * Getter for selected items from table
   */
  public getSelectedItems(): any {
    return this.selection.selected;
  }
  /**
   * Method to clear all selection from Table
   */
  public clearSelection() {
    this.selection.clear();
  }
  public toggleRowExpansion(row) {
    if (this.isExpandable) {
      this.expandedElement = this.expandedElement === row ? null : row;
    }
  }
  /**
   * Utility method to getRowData
   * @param row - row name
   */
  public getRowData(row: string) {
    const column: AwardDataColumn = this.displayedColumns.filter(o => o.bindingKey === row)[0];
    if (column.type === 'date') {
      return this.tableData && this.tableData.data.map(o => this.datepipe.transform(o[row], 'yyyy-MM-dd HH:mm'));
    } else {
      return this.tableData && this.tableData.data.map(o => o[row]);
    }
  }
  /**
   * Utility method to check if all items are selected
   */
  public isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.tableData.filteredData.length ?
                    this.tableData.filteredData.length :
                    this.tableData.data.length;
    return numSelected === numRows;
  }
  /**
   * Handler for clearing the filters
   */
  clearFilter() {
    if (this.searchComponents) {
      this.searchComponents.forEach(searchComponent => searchComponent.clearFilter());
    }
  }
  /**
   * Hanlder for applying filter on livechange of text in filter
   */
  public applyFilter(filters: string[], column: string) {
    this.filters[column] = filters;
    this.tableData.filter = this.filters;
  }
  /**
   * Selects all rows if they are not all selected; otherwise clear selection
   */
  public masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      (this.tableData.filteredData.length ? this.tableData.filteredData : this.tableData.data)
      .forEach(row => this.selection.select(row));
    this.selectionChange.emit(this.getSelectedItems());
  }
  /**
   * Handler for selection/deselection of items and to emit the change event
   * @param row - Row to be selected/deselected
   */
  public onSingleSelectionChange(row: object) {
    this.selection.toggle(row);
    this.selectionChange.emit(this.getSelectedItems());
  }
  /**
   * Utility method to handle the table selection mode
   */
  private _handleMode() {
    if ((this.mode === 'MultiSelect' || this.mode === 'SingleSelect') && !this.allColumns.includes('select')) {
      this.allColumns.unshift('select');
    } else if (this.mode === 'None') {
      let index = this.allColumns.indexOf('select');
      index >= 0 ? index = index : index = this.allColumns.indexOf('action');
      if (index >= 0) {
        this.allColumns.splice(index, 1);
      }
    }
  }
  /**
   * Utility method to write the filter predicate for table
   * @param data - table data
   * @param filters - key value pair of <column> : <filter string>
   */
  private getTableFilterPredicate(data: object, filters: TableFilter) {
    const matchFilter = [];
    const columns = Object.keys(filters);
    columns.forEach(column => {
      const searchValues: string[] = filters[column];
      if (searchValues.length) {
        const customFilter = [];
        searchValues.forEach(value => {
          if (!data[column]) {
            return customFilter.push(false);
          }
          const type = this.displayedColumns.filter(o => o.bindingKey === column)[0].type;
          if (type === 'date') {
            const isEqual = this.datepipe.transform(data[column], 'dd-MM-yy') === value;
            return customFilter.push(isEqual);
          } else {
            return customFilter.push(data[column].toString().includes(value));
          }

        });
        matchFilter.push(customFilter.some(Boolean)); // OR
      }
    });
    return matchFilter.every(Boolean); // AND
  }
  get isSearchVisibleForSomeColumn() {
    return this.displayedColumns && this.displayedColumns.filter(c => c.searchVisible === true).length > 0;
  }
}
